.app-fade-slide-in-animation {
  animation: fadeInSlideAnimation ease 0.7s ;
  animation-iteration-count: 1; 
  animation-fill-mode: forwards; 
}
.fadeee {
  opacity: 0;
}
@keyframes fadeInSlideAnimation { 
  0% { 
      opacity: 0;
      transform: translateY(20%); 
  } 

  100% { 
      opacity: 1;
      transform: translateY(0);
   } 
} 